.side-nav-bar {
  position: fixed;
  top: 10%;
  left: 0;
  height: 90%;
  background-color: $background-elements;
  width: 15%;
  z-index: 0;
  display: flex;
  align-items: center;
  @media (max-width: 1030px) {
    @include center();
    width: 100%;
    height: 4rem;
    backdrop-filter: blur(5px);
    background-color: $back-header;
    border: 1px solid $death-green;
    z-index: 999;
  }
}

#genre-list {
  display: flex;
  flex-direction: column;

  @media (max-width: 1030px) {
    flex-direction: row;
    gap: $spacing;
  }

  li {
    letter-spacing: $letter-spacing;
    color: white;
    font-weight: $primary-font-weight;
    transition: $transition;

    padding: 5px 1.5rem;
    position: relative;
    span {
      position: relative;
      &::after {
        content: '';
        width: 0;
        height: 2px;
        position: absolute;
        bottom: -4px;
        background: $death-green;
        transition: width 0.5s, left 0.5s;
        right: 0;
      }

      &:hover::after {
        width: 100%;
        left: 0;
      }
    }

    &:hover {
      color: $primary-color;
      cursor: pointer;
    }
  }
}

@media (max-width: 1030px) {
  .marquee-container {
    width: 100%;
    overflow: hidden;

    #genre-list {
      white-space: nowrap;
      display: inline-block;
      animation: marquee 20s linear infinite;

      &:hover {
        animation-play-state: paused;
      }

      li {
        display: inline-block;
        padding: 0 10px;
      }
    }
  }

  @keyframes marquee {
    0% {
      transform: translateX(60%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
}
